(function() {
  'use strict';

  angular
    .module('app.admin.panel')
    .controller('Panel', Panel)

  Panel.$inject = [
    'dishesCreateModal',
    'dishesEditModal',
    'categoriesCreateModal',
    'helpers',
    'categories',
    'images',
    '$timeout',
    '$q',
    'toastr'
  ];

  function Panel(
    dishCreateModal,
    dishEditModal,
    categoryModal,
    helpers,
    categories,
    images,
    $timeout,
    $q,
    toastr
  ) {
    let vm = this;

    dishCreateModal.scope = vm;

    vm.spinner              = true;
    vm.loadDishes           = loadDishes;
    vm.removeDish           = removeDishFromCategory;
    vm.removeCategory       = removeCategory;
    vm.showModalDishAdd     = dishCreateModal.show;
    vm.showModalCategoryAdd = categoryModal.show;
    vm.showModalDishEdit    = showDishEditModal;
    vm.selectedCategory;

    $q.all([activate()]).then(() => {
      hideSpinner();
    });

    ////////////

    function activate() {
      return categories.fetchAll().$promise
        .then((categories) => {
          vm.categories = categories;

          return categories[0];
        }).then((firstCategory) => {
          return categories.fetchDishes({
            id: firstCategory.id
          }).$promise.then(dishes => {
            vm.selectedCategory = firstCategory;
            vm.dishes           = dishes;

            return vm.dishes;
          });
        }).then(() => {
          return getDishesImages(vm.dishes);
        });
    }

    function loadDishes(category) {
      vm.spinner = true;

      categories.fetchDishes({
        id: category.id
      }, dishes => {
        vm.selectedCategory = category;
        vm.dishes           = dishes;

        getDishesImages(vm.dishes).then(() => {
          hideSpinner();
        });
      });
    }

    function removeCategory() {
      let msg = `     - ${vm.selectedCategory.name}?`;

      if (confirm(msg)) {
        categories.remove({
          id: vm.selectedCategory.id
        }, () => {
          let search = helpers.searchByKey(vm.categories, {
            id: vm.selectedCategory.id
          });

          vm.categories.splice(vm.categories.indexOf(search), 1);
          vm.selectedCategory = vm.categories[0];
          loadDishes(vm.categories[0]);

          toastr.success(' ');
        });
      }
    }

    function getDishesImages(dishes) {
      let filter = dishes.filter(dish => {
        if (dish.images.length) {
          return true;
        }
      });

      let before = [];

      filter.forEach(dish => {
        let obj = {
          dishId: dish.id,
          links: []
        };

        dish.images.forEach(image => {
          obj.links.push(image.link);
        });

        before.push(obj);
      });

      return images.fetchAll(before).$promise
        .then(imagesLink => {
          imagesLink.forEach(images => {
            let search = helpers.searchByKey(dishes, {
              id: images.dishId
            });

            if (search) {
              search.images = images.images;
            }
          });
        });
    }

    function removeDishFromCategory(idDish) {
      let msg = `    ?`;

      if (confirm(msg)) {
        categories.removeDish({
          id: vm.selectedCategory.id,
          dishes: [{
            id: idDish
          }]
        }, () => {
          let search = helpers.searchByKey(vm.dishes, {
            id: idDish
          });

          vm.dishes.splice(vm.dishes.indexOf(search), 1);
          toastr.success(' ');
        });
      }
    }

    function showDishEditModal(dish) {
      let data = angular.extend({
        categoryId: vm.selectedCategory.id
      }, dish);
      
      dishEditModal.scope = data;
      dishEditModal.show();
    }

    function hideSpinner() {
      $timeout(() => {
        vm.spinner = false;
      }, 400);
    }

    /**
     *      
     */

    dishCreateModal.on(dishCreateModal.events.create, () => {
      // Reload list of dishes
      categories.fetchDishes({
        id: vm.selectedCategory.id
      }, dishes => {
        getDishesImages(dishes).then(() => {
          vm.dishes = dishes;
        });
      });
    });

    /**
     *     
     */

    dishEditModal.on(dishEditModal.events.update, updateDish => {
      // Reload list of dishes
      categories.fetchDishes({
        id: vm.selectedCategory.id
      }, dishes => {
        getDishesImages(dishes).then(() => {
          vm.dishes = dishes;
        });
      });
    });

    /**
     *      
     */

    categoryModal.on(categoryModal.events.create, () => {
      categories.fetchAll({}, (categories) => {
        vm.categories = categories;
      });
    });
  }
})();