(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('dishesEditModal', editDish)
    .controller('dishesEditModalCtrl', dishesEditModalCtrl);

  editDish.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function editDish(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      update: 'update'
    };

    let modal   = new BaseModal('dishesEditModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/dishes/edit.html',
        width: '60%',
        showClose: false,
        controller: 'dishesEditModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  dishesEditModalCtrl.$inject = [
    'dishes',
    'BaseModal',
    'toastr'
  ];

  function dishesEditModalCtrl(
    dishes,
    BaseModal,
    toastr
  ) {
    let vm     = this;
    let modal  = BaseModal.get('dishesEditModal');
    let images = modal.scope.images;

    vm.update  = updateDish;
    vm.spinner = false;
    vm.form    = {
      images: images,
      id: modal.scope.id,
      name: modal.scope.name,
      price: modal.scope.price,
      description: modal.scope.description,
      type: modal.scope.type,
      categoryId: modal.scope.categoryId
    };

    vm.types = [{
      name: '',
      value: 'ORDINARY'
    }, {
      name: '',
      value: 'LATEST'
    }, {
      name: ' ',
      value: 'HOT'
    }, {
      name: ' ',
      value: 'VEGAN'
    }];

    ////////////////

    function updateDish() {
      vm.spinner = true;
      dishes.update(vm.form, updateDish => {
        vm.spinner = false;

        modal.hide();
        toastr.success(' ');

        modal.emit(modal.events.update, updateDish);
      }, () => {
        vm.spinner = false;
      })
    }
  }
})();