(function() {
  angular.module('blocks.helpers', [])
    .factory('helpers', helpers)

  helpers.$inject = [];

  function helpers() {

    var service = {};

    /**
     *      ,     sizeColumn 
     * Helpers.shapeArrayInRows([0,1,2,3], 3) => [[0,1,2],[3]]
     * @param {Array} array
     * @param {Integer} sizeColumn
     * @returns {Array}
     */

    service.shapeArrayInRows = (array, sizeColumn) => {
      var result = [];

      for (var i = 0; i < array.length; i += sizeColumn) {
        result.push(array.slice(i, i + sizeColumn));
      }

      return result;
    }

    /**
     *     data,    conditions.
     *   conditions = {key: value}
     * @param {Array} data
     * @param {Object} conditions
     * @returns {Object|Undefined}
     */

    service.searchByKey = (data, conditions) => {
      let keys = Object.keys(conditions);
      let result;

      data.forEach((row) => {
        if (row[keys[0]] == conditions[keys[0]]) {
          result = row;
        }
      });

      return result;
    }
    
    service.searchIndex = (data, conditions) => {
      let keys = Object.keys(conditions);
      let index;
      
      for(let i = 0; i < data.length; i++) {
        if (data[i][keys[0]] == conditions[keys[0]]) {
          index = i;
        }
      }

      return index;
    }

    service.nextElementInArray = (data, conditions) => {
      let keys = Object.keys(conditions);
      let result;

      data.forEach((row, i, array) => {
        if (row[keys[0]] == conditions[keys[0]]) {
          result = array[i + 1];
        }
      });

      return result;
    }

    service.prevElementInArray = (data, conditions) => {
      let keys = Object.keys(conditions);
      let result;

      data.forEach((row, i, array) => {
        if (row[keys[0]] == conditions[keys[0]]) {
          result = array[i - 1];
        }
      });

      return result;
    }

    /**
     *       
     * @param array
     * @param string
     * @returns {*}
     */

    service.findString = (array, string) => {
      let result = false;

      array.forEach((item) => {
        if (item === string) {
          result = item;
        }
      });

      return result;
    }

    /**
     *    id   
     * @param prev
     * @param next
     * @returns {number}
     */

    service.compareById = (prev, next) => {
      if (prev.id > next.id) {
        return -1;
      }

      if (prev.id < next.id) {
        return 1;
      }

      return 0;
    }

    /**
     *      
     * @param {Array} orders
     * @param {Array} statuses
     * @returns {Array}
     */

    service.filterOrdersByStatus = (orders, statuses) => {
      let results = [];

      orders.forEach((order) => {
        if (service.findString(statuses, order.status)) {
          results.push(order);
        }
      });

      return results;
    }

    /**
     *     
     * @param order
     * @returns {{price: number, count: number}}
     */
    
    service.calculateOrderInfo = (order) => {
      let result = {
        price: 0,
        count: 0
      };

      for (let dish of order.dishes) {
        result.price += dish.dish.price * dish.quantity;
        result.count += dish.quantity;
      }

      return result;
    }

    return service;
  }
})();
