(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('dishesCreateModal', createDish)
    .controller('dishesCreateModalCtrl', createDishCtrl);

  createDish.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function createDish(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      create: 'create'
    };

    let modal   = new BaseModal('dishesCreateModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/dishes/create.html',
        width: '60%',
        showClose: false,
        controller: 'dishesCreateModalCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  createDishCtrl.$inject = [
    'dishes',
    'BaseModal',
    'toastr'
  ];

  function createDishCtrl(
    dishes,
    BaseModal,
    toastr
  ) {
    let vm    = this;
    let modal = BaseModal.get('dishesCreateModal');

    vm.create  = createDish;
    vm.spinner = false;
    vm.form    = {
      images: [],
      name: '',
      price: '',
      description: '',
      type: 'ORDINARY',
      categoryId: modal.scope.selectedCategory.id
    };

    vm.types = [{
      name: '',
      value: 'ORDINARY'
    }, {
      name: '',
      value: 'LATEST'
    }, {
      name: ' ',
      value: 'HOT'
    }, {
      name: ' ',
      value: 'VEGAN'
    }];

    ////////////////

    function createDish() {
      vm.spinner = true;
      dishes.create(vm.form, () => {
        vm.spinner = false;

        modal.hide();
        toastr.success(' ');

        modal.emit(modal.events.create);
      }, () => {
        vm.spinner = false;
      });
    }
  }
})();
