(function() {
  'use strict';

  angular
    .module('app.modals')
    .factory('managerOrderDetailModal', orderDetail)
    .controller('managerOrderDetailCtrl', orderDetailCtrl);

  orderDetail.$inject = [
    'ngDialog',
    'BaseModal'
  ];

  /* @ngInject */
  function orderDetail(
    ngDialog,
    BaseModal
  ) {
    // Events that emit controller
    let events = {
      update: 'update',
      create: 'create',
      decline: 'decline'
    };

    let modal   = new BaseModal('managerOrderDetailModal', events);
    let service = angular.extend(modal, {
      show: showModal,
      hide: hideModal
    });

    return service;

    ////////////////

    function showModal() {
      ngDialog.open({
        template: 'html/modals/manager/order-detail.html',
        showClose: false,
        appendClassName: BaseModal.getClassName() + ' order-detail',
        controller: 'managerOrderDetailCtrl',
        controllerAs: 'vm'
      });
    }

    function hideModal() {
      ngDialog.close();
    }
  }

  orderDetailCtrl.$inject = [
    'BaseModal',
    'dishesSelectModal',
    'orderHistoryModal',
    'admin',
    'helpers',
    'validator',
    'toastr',
    '$scope',
    'orders'
  ];

  function orderDetailCtrl(
    BaseModal,
    selectModal,
    historylModal,
    admin,
    helpers,
    validator,
    toastr,
    $scope,
    orders
  ) {
    let vm          = this;
    let modal       = BaseModal.get('managerOrderDetailModal');
    let clientRules = [{
      field: 'name',
      rules: ['required']
    }, {
      field: 'address',
      rules: ['required']
    }, {
      field: 'phoneNumber',
      rules: ['required', 'phone']
    }];

    let orderRules = [{
      field: 'dishes',
      rules: ['required']
    }];

    //     "",    - 
    //    
    vm.createEnabled = modal.scope.createEnabled;

    if (vm.createEnabled) {
      let orderFrame = {
        client: {
          address: '',
          name: '',
          phoneNumber: ''
        },
        dishes: [],
        status: 'FRESH'
      };

      vm.order = orderFrame;
    } else {
      vm.order = modal.scope.order;
    }

    vm.closeMessage     = '';
    vm.showCloseMessage = (vm.closeMessage) ? true : false;
    vm.closeBtnMessage  = (vm.closeMessage) ? '' : ' ';
    vm.orderInfo        = helpers.calculateOrderInfo(vm.order);
    vm.orderValid       = true;

    //   ,    
    vm.selectDishesModal  = showSelectModal;
    vm.historyOrdersModal = showHistoryModal;
    //  
    vm.incQuantity        = incQuantity;
    vm.decQuantity        = decQuantity;
    vm.removeDish         = removeDish;
    // . 
    vm.showCloseOrderForm = showCloseOrderForm;
    vm.hideCloseOrderForm = hideCloseOrderForm;
    //   (   )
    vm.saveOrder          = saveOrder;

    ////////////////

    /**
     *  - 
     * @param {Object} dish
     */

    function incQuantity(dish) {
      let search = helpers.searchByKey(vm.order.dishes, {dishId: dish.dishId});
      search.quantity++;
    }

    /**
     *  - 
     * @param {Object} dish
     */

    function decQuantity(dish) {
      let search = helpers.searchByKey(vm.order.dishes, {dishId: dish.dishId});
      search.quantity--;

      if (!search.quantity) {
        if (!lastDishInOrder()) {
          //    
          vm.order.dishes.splice(vm.order.dishes.indexOf(search), 1)
        } else {
          search.quantity++;
        }
      }
    }

    /**
     *    
     * @param {Object} dish
     */

    function removeDish(dish) {
      if (!lastDishInOrder()) {
        let search = helpers.searchByKey(vm.order.dishes, {dishId: dish.dishId});
        vm.order.dishes.splice(vm.order.dishes.indexOf(search), 1);
      }
    }

    //       
    function lastDishInOrder() {
      if (!(vm.order.dishes.length - 1)) {
        toastr.error('    !');
        return true;
      }

      return false;
    }

    /**
     *     . 
     */

    function showSelectModal() {
      //     two data binding
      let copy = angular.copy(vm.order);

      selectModal.scope.dishes = copy.dishes;
      selectModal.show();
    }

    /**
     *      
     */

    function showHistoryModal() {
      admin.history({
        clientPhone: vm.order.client.phoneNumber
      }, result => {
        historylModal.scope = {
          orders: result,
          hideBack: true
        };

        historylModal.show();
      });
    }

    /**
     *    
     */

    function showCloseOrderForm() {
      if (vm.showCloseMessage == true) {
        if (vm.closeMessage !== '') {
          let data = {
            id: vm.order.id,
            message: vm.closeMessage,
            status: 'DECLINED'
          };

          // (Call )     
          orders.decline(data, () => {
            toastr.success(' ');
          });

          modal.emit(modal.events.decline, vm.order);
          modal.hide();
        } else {
          toastr.error('    ');
        }
      }

      vm.showCloseMessage = true;
      vm.closeBtnMessage  = '';
    }

    function hideCloseOrderForm() {
      vm.showCloseMessage = false;
      vm.closeBtnMessage  = ' ';
    }

    /**
     *       
     */

    function saveOrder() {
      if (vm.order.dishes.length) {
        if (vm.createEnabled) {
          createOrder();
        } else {
          updateOrder();
        }
      } else {
        toastr.error('    !');
      }
    }

    function createOrder() {
      vm.order.status = 'KITCHEN';

      //    
      let orderData = {
        id: vm.order.id,
        client: {
          name: vm.order.client.name,
          phone: vm.order.client.phoneNumber,
          address: vm.order.client.address
        },
        dishes: vm.order.dishes,
        status: vm.order.status
      };

      modal.hide();

      orders.create(orderData, order => {
        modal.emit(modal.events.create, order);
        toastr.success(' ');
      });
    }

    function updateOrder() {
      vm.order.status = 'KITCHEN';

      //    
      let orderData = {
        id: vm.order.id,
        client: {
          name: vm.order.client.name,
          phone: vm.order.client.phoneNumber,
          address: vm.order.client.address
        },
        dishes: vm.order.dishes,
        status: vm.order.status
      };

      modal.emit(modal.events.update, vm.order);
      modal.hide();

      orders.update(orderData, () => {
        toastr.success(' ');
      });
    }

    /**
     *      
     * @param {Object }order
     * @returns {boolean}
     */

    function validateOrder(order) {
      let result = false;

      let clientReport = validator.run(order.client, clientRules);
      let orderReport  = validator.run(order, orderRules);

      if (!(clientReport.errors.length) && !(orderReport.errors.length)) {
        result = true;
      }

      return result;
    }

    /**
     *  orderInfo
     */

    $scope.$watch(function() {
      return vm.order.dishes;
    }, function() {
      vm.orderInfo = helpers.calculateOrderInfo(vm.order);
    }, true);

    /**
     *  
     */

    $scope.$watch(function() {
      return vm.order;
    }, function(newValue) {
      if (validateOrder(newValue)) {
        vm.orderValid = true;
      } else {
        vm.orderValid = false;
      }
    }, true);


    /**
     *   
     */

    selectModal.on(selectModal.events.selected, dishes => {
      vm.order.dishes = dishes;
    });
  }
})();

